import React from "react";
import {Nav} from "react-bootstrap";
import '../stylesheets/sidebar.css'
import styled from 'styled-components'
import animateScrollTo from "animated-scroll-to";
import { Link } from "react-router-dom";
import { isBrowser } from 'react-device-detect';

const Item = styled.h3`

  font-size: 0.8em;
  text-align: center;
  color: black;
  cursor: pointer;
  transition: transform 100ms ease-in-out;
  &:hover {
    transform: scale(1.2);
    /* font-weight: 200; */
    /* text-decoration: underline; */
  }
`;

function StandardItems() {

    if (isBrowser) {
      return <div className="sidebar-content" >
        <Nav.Item>
            <Nav.Link onClick={() => {
                animateScrollTo(document.querySelector("#home"));}} 
                ><Item>O'howdy</Item></Nav.Link>
        </Nav.Item>
        <Nav.Item>
            <Nav.Link onClick={() => {
                animateScrollTo(document.querySelector("#generative"));}} 
                ><Item>Generative Projects</Item></Nav.Link>
        </Nav.Item>
        <Nav.Item>
            <Nav.Link 
            onClick={() => {animateScrollTo(document.querySelector("#music"));}} 
            ><Item>Music</Item></Nav.Link>
        </Nav.Item>
        ------
        <Link to="/blog"><Item>Blog</Item></Link>
        <Link to="/pictures"><Item>Illustrations</Item></Link>
    </div>
    }
    else {
      return <div className="sidebar-content" >
      <Link to="/blog"><Item>Blog</Item></Link>
  </div>
    }
  }


const Sidebar = props => {
    return (
        <>
            <Nav className="col-md-12 sidebar">
                <StandardItems />
            </Nav>

        </>
        );
  };
  export default Sidebar